import { useState, useMemo, useCallback } from 'react';
import intervalToDuration from 'date-fns/intervalToDuration';
import addMilliseconds from 'date-fns/addMilliseconds';
import { isBefore, isAfter } from 'date-fns';
import { useInterval } from '@lubycon/react';
import sandboxData from 'data/sandbox.json';
import { SandboxApplyStatus } from 'src/models/sandbox';
import { SandboxApplyFormData } from 'src/models/forms';
import { applySandbox } from 'src/remotes/lubycon';

const intervalTime = 1000;

const { applyStartDate, applyEndDate, enableApply, group } = sandboxData;
const startDate = new Date(applyStartDate);
const endDate = new Date(applyEndDate);

export function useSandboxApply() {
  const [now, setTime] = useState(new Date());

  const count = useMemo(() => {
    return intervalToDuration({
      start: now,
      end: endDate,
    });
  }, [now]);

  const status = useMemo(() => {
    if (enableApply === false) {
      return SandboxApplyStatus.강제종료;
    } else if (isBefore(now, startDate)) {
      return SandboxApplyStatus.사전신청;
    } else if (isAfter(now, endDate)) {
      return SandboxApplyStatus.마감;
    } else {
      return SandboxApplyStatus.신청기간;
    }
  }, [now]);

  const apply = useCallback((data: SandboxApplyFormData) => {
    const { response } = applySandbox({ ...data, group });
    return response;
  }, []);

  useInterval(() => {
    setTime(v => addMilliseconds(v, intervalTime));
  }, intervalTime);

  return { count, status, apply };
}
